import React from 'react';

import MainRoute from 'routes';

import { Container } from './styled';
import { BeforeInstallPromptEvent } from 'model/beforeInstallPrompt';

type Props = {};

class App extends React.Component<Props> {
  deferredInstallPrompt: BeforeInstallPromptEvent | null = null;

  componentDidMount(): void {
    window.addEventListener(
      'beforeinstallprompt',
      this.saveBeforeInstallPromptEvent,
    );
  }

  saveBeforeInstallPromptEvent = (event: BeforeInstallPromptEvent): void => {
    this.deferredInstallPrompt = event;
    console.log('savebeforeInstall', this.deferredInstallPrompt);
  };

  render(): JSX.Element {
    console.log('beforeInstall', this.deferredInstallPrompt);
    return (
      <Container>
        <MainRoute />
      </Container>
    );
  }
}

export default App;
